.element {
    max-width: 100%;
    max-height: 361px;
    background-color: white;
    padding: 0;
    margin: 0;
    list-style: none;
    overflow: hidden;
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
